<template>
  <div>
    <!-- BreadCrumb.vue-------------- -->
    <bread-crumb one="" two="" three="reports"></bread-crumb>

    <!-- -------------------------------------- -->
    <el-card class="box-card">
      <!--  -->
    <div id="report" ref="rep" style="width:750px; height:400px;"></div>
    </el-card>

    <el-card class="box-card">
      <!--  -->
    <div id="report1" ref="rep1" style="width:750px; height:400px;"></div>
    </el-card>

    <el-card class="box-card">
      <!--  -->
    <div id="report2" ref="rep2" style="width:750px; height:400px;"></div>
    </el-card>

    <el-card class="box-card">
      <!--  -->
    <div id="report3" ref="rep3" style="width:750px; height:400px;"></div>
    </el-card>
  </div>
</template>

<script>
// 
import echarts from 'echarts'

export default {
  // Vuediv
  mounted () {
    // 
    this.paintDataPic()

    // 
    this.pieChart()

    // 
    this.lineChart()

    // 
    this.recChart()
  },

  methods: {
    // 
    paintDataPic () {
      // domecharts
      // var myChart = echarts.init(document.getElementById('report'))
      // vue$refs
      var myChart = echarts.init(this.$refs.rep)
      // 
      myChart.setOption({
        title: {
          text: 'ECharts '
        },
        tooltip: {},
        xAxis: {
          data: ['', '', '', '', '', '']
        },
        yAxis: {},
        series: [{
          name: '',
          type: 'bar',
          data: [5, 20, 36, 10, 10, 20]
        }]
      })
    },

    // 
    pieChart () {
      // domecharts
      // var myChart = echarts.init(document.getElementById('report1'))
      var myChart = echarts.init(this.$refs.rep1)

      // 
      myChart.setOption({
        title: {
          text: '',
          subtext: '',
          x: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          left: 'left',
          data: ['', '', '', '', '']
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: '65%',
            center: ['65%', '55%'],
            data: [
              { value: 335, name: '' },
              { value: 310, name: '' },
              { value: 234, name: '' },
              { value: 135, name: '' },
              { value: 1000, name: '' }
            ],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      })
    },

    // 
    lineChart () {
      // domecharts
      // var myChart = echarts.init(document.getElementById('report2'))
      var myChart = echarts.init(this.$refs.rep2)

      // 
      myChart.setOption({
        title: {
          text: ''
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985'
            }
          }
        },
        legend: {
          data: ['', '', '', '', '']
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            boundaryGap: false,
            data: ['2019-7-12', '2019-7-13', '2019-7-14', '2019-7-15', '2019-7-16', '2019-7-17', '2019-7-18']
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ],
        series: [
          {
            name: '',
            type: 'line',
            stack: '',
            areaStyle: {},
            data: [1200, 1320, 1010, 1340, 900, 2300, 2100]
          },
          {
            name: '',
            type: 'line',
            stack: '',
            areaStyle: {},
            data: [2200, 1802, 1901, 2340, 2900, 3300, 3100]
          },
          {
            name: '',
            type: 'line',
            stack: '',
            areaStyle: {},
            data: [1500, 2302, 2010, 1504, 1900, 3300, 4100]
          },
          {
            name: '',
            type: 'line',
            stack: '',
            areaStyle: { normal: {} },
            data: [3200, 3320, 3010, 3340, 3900, 3300, 3200]
          },
          {
            name: '',
            type: 'line',
            stack: '',
            label: {
              normal: {
                show: true,
                position: 'top'
              }
            },
            areaStyle: { normal: {} },
            data: [8200, 9302, 9001, 9304, 12900, 13300, 13200]
          }
        ]
      })
    },

    // 
    recChart () {
      // domecharts
      // var myChart = echarts.init(document.getElementById('report3'))
      var myChart = echarts.init(this.$refs.rep3)

      // 
      myChart.setOption({
        series: [{
          type: 'treemap',
          data: [{
            name: 'nodeA', // First tree
            value: 10,
            children: [{
              name: 'nodeAa', // First leaf of first tree
              value: 4
            }, {
              name: 'nodeAb', // Second leaf of first tree
              value: 6
            }]
          }, {
            name: 'nodeB', // Second tree
            value: 20,
            children: [{
              name: 'nodeBa', // Son of first tree
              value: 20,
              children: [{
                name: 'nodeBa1', // Granson of first tree
                value: 20
              }]
            }]
          }]
        }]
      })
    }
  }

}
</script>

<style lang="less" scoped>
</style>
