<template>
  <div>
    <!-- BreadCrumb.vue-------------- -->
    <bread-crumb one="" two="" three="users"></bread-crumb>

    <!-- ---------------------------------- -->
    <el-dialog
      title=""
      :visible.sync="addDialog"
      width="50%"
      @close="$refs.addFormRef.resetFields()"
    >
      <el-form ref="addFormRef" :rules="addFormRules" :model="addForm" label-width="70px">
        <!--  -->
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username" ref="myname" clearable @clear="getUserList()"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="password">
          <el-input
            v-model="addForm.password"
            show-password
            ref="mypass"
            clearable
            @clear="getUserList()"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email" ref="myemail" clearable @clear="getUserList()"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="mobile">
          <el-input v-model="addForm.mobile" ref="mymobile" clearable @clear="getUserList()"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialog = false"> </el-button>
        <el-button type="primary" @click="addUser()"></el-button>
      </span>
    </el-dialog>

    <!-- ---------------------------- -->
    <el-dialog title="" :visible.sync="editDialog" width="50%" @close="editForm={}">
      <el-form ref="editFormRef" :rules="editFormRules" :model="editForm" label-width="70px">
        <!--  -->
        <el-form-item label="" prop="username">
          <el-input
            v-model="editForm.username"
            ref="myname"
            clearable
            @clear="getUserList()"
            disabled
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="email">
          <el-input
            v-model="editForm.email"
            ref="myemail"
            clearable
            @keyup.enter.native="editUser()"
            @clear="getUserList()"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="mobile">
          <el-input
            v-model="editForm.mobile"
            ref="mymobile"
            clearable
            @keyup.enter.native="editUser()"
            @clear="getUserList()"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialog = false"> </el-button>
        <el-button type="primary" @click="editUser()"></el-button>
      </span>
    </el-dialog>

    <!-- ----------------------------- -->
    <el-dialog title="" :visible.sync="assigDialog" width="50%" @close="$refs.assigFormRef.resetFields()">
      <el-form ref="assigFormRef" :model="assigForm" label-width="120px">
        <!--  -->
        <el-form-item label="" prop="username">{{assigForm.username}}</el-form-item>
        <!--  -->
        <el-form-item label="" prop="role_name">{{assigForm.role_name}}</el-form-item>
        <!--  -->
        <el-form-item label="" prop="rid">
          <!--  -->
          <el-select v-model="assigForm.rid" placeholder="">
            <el-option
              v-for="item in roleList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="assigDialog = false"> </el-button>
        <el-button type="primary" @click="assigUser()"></el-button>
      </span>
    </el-dialog>

    <!-- -------------------------------------- -->
    <el-card class="box-card">
      <!-- /------------------------------------- -->
      <el-row :gutter="20">
        <el-col :span="8">
          <!--  -->
          <el-input placeholder="" v-model="reqParam.query" clearable @clear="getUserList()" @keyup.enter.native="getUserList()">
            <!-- -------------------------------- -->
            <el-button slot="append" icon="el-icon-search" @click="getUserList()"></el-button>
          </el-input>
        </el-col>

        <!-- --------------------------------- -->
        <el-col :span="7">
          <el-button type="primary" @click="addDialog=true"></el-button>
        </el-col>
      </el-row>

      <!-- table----------------------------- -->
      <el-table :data="userList" style="width: 100%;" border>
        <el-table-column type="index" label="" width="50"></el-table-column>
        <el-table-column prop="username" label="" width="150"></el-table-column>
        <el-table-column prop="mobile" label="" width="250"></el-table-column>
        <el-table-column prop="role_name" label="" width="220"></el-table-column>
        <el-table-column prop="email" label="" width="250"></el-table-column>
        <el-table-column label="" width="100">
          <!--  -->
          <!-- mg_statetrue/false -->
          <!--  -->
          <el-switch slot-scope="stData" v-model="stData.row.mg_state" active-color="#13ce66" @change="modState(stData.row.id, stData.row.mg_state)"></el-switch>
        </el-table-column>

        <!-- ---------------------------------- -->
        <el-table-column label="" width="232">
          <!-- stDatarow/id -->
          <template slot-scope="stData">
            <!-- -------------------------------- -->
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(stData.row)"
            ></el-button>
            <!-- -------------------------------- -->
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="delUser(stData.row.id)"
            ></el-button>
            <!-- ------------------------------- -->
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button size="mini" type="warning" icon="el-icon-setting" @click="showAssigDialog(stData.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- ----------------------------------------- -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="reqParam.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="reqParam.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="sum"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    // valuecallback/
    var checkName = (rule, value, callback) => {
      var reg = /^[A-Za-z0-9\u4E00-\u9FA5_-]{3,16}$/ // 
      if (!reg.test(value)) {
        return callback(new Error(''))
      } else {
        callback()
      }
    }

    var checkPsw = (rule, value, callback) => {
      var reg = /^\d{6,}$/ // 
      if (!reg.test(value)) {
        return callback(new Error(''))
      } else {
        callback()
      }
    }

    var checkEmail = (rule, value, callback) => {
      var reg = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/
      if (!reg.test(value)) {
        // console.log(this.$refs.mymobile.getInput())
        this.$refs.myemail.getInput().style.border = '2px solid red'
        return callback(new Error(''))
      } else {
        this.$refs.myemail.getInput().style.border = '2px solid green'
        callback()
      }
    }

    var checkMobile = (rule, value, callback) => {
      var reg = /^1[3-9]\d{9}$/
      if (!reg.test(value)) {
        this.$refs.mymobile.getInput().style.border = '2px solid red'
        return callback(new Error(''))
      } else {
        this.$refs.mymobile.getInput().style.border = '2px solid green'
        callback()
      }
    }

    return {
      // 
      userList: [],
      // 
      sum: 0, // 
      reqParam: {
        query: '', // 
        pagenum: 1, // 1
        pagesize: +window.localStorage.getItem('pagenum') || 3 // localStorage   3
      },

      // ----------------------------------------
      addDialog: false, // 
      // Form
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 
      addFormRules: {
        username: [
          // { required: , message: '', trigger: '' }
          { required: true, message: '', trigger: 'blur' },
          { validator: checkName, trigger: 'change' }
        ],

        password: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkPsw, trigger: 'change' }
        ],

        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'change' }
        ],

        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'change' }
        ]
      },

      // ----------------------------------------
      editDialog: false, // 
      // Form
      editForm: {
        username: '',
        email: '',
        mobile: ''
      },
      // 
      editFormRules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'change' }
        ],

        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'change' }
        ]
      },

      // --------------------------------------------
      assigDialog: false, // 
      // 
      assigForm: {
        id: '', // id
        username: '',
        role_name: '',
        rid: '' // 
      },
      // roleList
      roleList: []
    }
  },

  // 
  created () {
    this.getUserList()
  },

  methods: {
    // getUserList()----------------------
    async getUserList () {
      var { data: dt } = await this.$http.get('users', {
        params: this.reqParam
      })
      // console.log(dt)
      // 
      this.sum = dt.data.total
      // 
      /* if (dt.meta.status !== 200) {
        return this.$message.error(dt.meta.msg)
      } */
      // userList
      this.userList = dt.data.users
    },

    // -----------------------------------------------
    handleSizeChange (newsize) {
      // 
      // console.log(newsize) // 
      // reqParam.pagesize
      console.log(newsize)
      this.reqParam.pagesize = newsize
      // newsizepagenumlocalStorage
      window.localStorage.setItem('pagenum', newsize)
      this.getUserList() // 
    },

    handleCurrentChange (newpage) {
      // 
      // console.log(newpage) // 
      // reqParam.pagenum
      this.reqParam.pagenum = newpage
      this.getUserList() // 
    },

    // --------------------------------------
    addUser () {
      // 
      console.log(this.$refs.addFormRef)
      this.$refs.addFormRef.validate(async valid => {
        if (valid) {
          // axios
          var { data: dt } = await this.$http.post('users', this.addForm)
          // console.log(dt)
          // 
          this.addDialog = false
          // 
          this.$message.success(dt.meta.msg)
          // 
          this.getUserList()
        }
      })
    },

    // ----------------------------------------
    delUser (uid) {
      // 
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          var { data: dt } = await this.$http.delete('users/' + uid)
          // console.log(dt)
          // 
          this.$message.success(dt.meta.msg)

          // 
          if (this.reqParam.pagenum !== 1 && this.userList.length === 1) {
            this.reqParam.pagenum--
          }

          // 
          this.getUserList()
        })
        .catch(() => {})
    },

    // ----------------------------
    showEditDialog (user) {
      // 
      this.editDialog = true
      // userid/username/email/mobile/password...
      // this.editForm = user
      this.editForm = Object.assign({}, user) // usereditForm
      // console.log(this.userList)
      // console.log(user)
    },

    // ------------------------------
    editUser () {
      this.$refs.editFormRef.validate(async valid => {
        if (valid) {
          var { data: dt } = await this.$http.put(
            'users/' + this.editForm.id,
            this.editForm
          )
          console.log(dt)
          // @close="$refs.editFormRef.clearValidate()"
          this.$refs.editFormRef.clearValidate() // 
          // 
          this.editDialog = false
          // 
          this.$message.success(dt.meta.msg)
          // 
          this.getUserList()
        }
      })
    },

    // ----------------------------------------
    async showAssigDialog (user) {
      // console.log(user)
      this.assigDialog = true // 
      // user
      this.assigForm = user
      // 
      var { data: dt } = await this.$http.get('roles')
      // console.log(dt)
      // roleList
      this.roleList = dt.data
    },

    // 
    async assigUser () {
      // 
      if (this.assigForm.rid === undefined) {
        this.$message.error('')
      } else {
        // axiosput
        var { data: dt } = await this.$http.put('users/' + this.assigForm.id + '/role', { rid: this.assigForm.rid })
        // console.log(dt)
        this.$refs.assigFormRef.clearValidate() // 
        // 
        this.assigDialog = false
        // 
        this.$message.success(dt.meta.msg)
        // 
        this.getUserList()
      }
    },

    // 
    async modState (uid, state) {
      // console.log(uid, state)
      var { data: dt } = await this.$http.put('users/' + uid + '/state/' + state)
      // console.log(dt)
      this.$message.success(dt.meta.msg)
      this.getUserList()
    }
  },

  // data---------------------------------
  watch: {
    // input
    'addForm.username': function (newval) {
      // console.log(newval, oldval)
      if (newval.trim().length > 2) {
        // console.log(this.$refs.mypass)
        this.$refs.myname.getInput().style.border = '2px solid green'
      } else if (newval.trim().length === 0) {
        this.$refs.myname.getInput().style.border = '2px solid #DCDFE6'
      } else {
        this.$refs.myname.getInput().style.border = '2px solid red'
      }
    },

    // input
    'addForm.password': function (newval) {
      // console.log(newval, oldval)
      if (newval.trim().length < 6) {
        // console.log(this.$refs.mypass)
        this.$refs.mypass.getInput().style.border = '2px solid red'
      } else {
        this.$refs.mypass.getInput().style.border = '2px solid green'
      }
    }

    // watch
    /* 'addForm.email': function (newval) {
      // console.log(newval, oldval)
      if (newval.trim().length > 0) {
        // console.log(this.$refs.mypass)
        this.$refs.myemail.getInput().style.border = '1px solid green'
      } else {
        this.$refs.myemail.getInput().style.border = '1px solid red'
      }
    }, */

    /* 'addForm.mobile': function (newval) {
      // console.log(newval, oldval)
      if (newval.trim().length > 11) {
        // console.log(this.$refs.mypass)
        this.$refs.mymobile.getInput().style.border = '1px solid green'
      } else {
        this.$refs.mymobile.getInput().style.border = '1px solid red'
      }
    } */
  }

}
</script>

<style lang="less" scoped>
</style>
