<template>
  <div class="login-container">
    <div class="login-box">
      <!-- logo -->
      <div class="avatar-box">
        <img src="../assets/img/jump.gif" alt />
      </div>

      <!-- form -->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules">
        <!--  -->
        <!-- <el-form-item prop=""> -->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" clearable @clear="login()" @keyup.enter.native="login()" ref="myname">
            <i slot="prefix" class="iconfont icon-user"></i>
          </el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input v-model="loginForm.password"  clearable @clear="login()" @keyup.enter.native="login()" show-password ref="mypass">
            <i slot="prefix" class="iconfont icon-3702mima"></i>
          </el-input>
        </el-form-item>
        <!--  -->
        <el-row>
          <el-col :push="15">
            <el-button type="primary" @click="login()"></el-button>
            <el-button type="info" @click="$refs.loginFormRef.resetFields()"></el-button>
          </el-col>
        </el-row>
      </el-form>
    </div>
  </div>
</template>

<script>
// icon
import '../assets/fonts/iconfont.css'

export default {
  data () {
    return {
      // 
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      // 
      loginFormRules: {
        // username/passwordprop
        username: [
          // { required: , message: '', trigger: '' }
          { required: true, message: '', trigger: 'blur' }
        ],
        password: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    // 
    login () {
      // home
      // console.log(this) // VueComponent
      // console.log(this.$refs.loginFormRef) // VueComponentel-form
      this.$refs.loginFormRef.validate(async valid => {
        // valid === true/false /
        // console.log(valid)
        if (valid) {
          // axios
          // this.$http.post('login',{username:this.loginForm.username,password:this.loginForm.password})
          var { data: dt } = await this.$http.post('login', this.loginForm)
          // var { data: dt }
          console.log(dt)
          // 200ElementUI
          /* if (dt.meta.status !== 200) {
            return this.$message.error(dt.meta.msg)
          } */

          // window.sessionStorage.setItem('token',data.data.token)
          window.sessionStorage.setItem('token', dt.data.token)

          // 
          this.$message.success(dt.meta.msg)

          // 
          this.$router.push('/home')
        }
      })
    }
  },
  // data
  watch: {
    'loginForm.username': function (newval) {
      // console.log(newval, oldval)
      if (newval.trim().length !== 0) {
        // console.log(this.$refs.mypass)
        this.$refs.myname.getInput().style.border = '2px solid green '
      } else {
        // this.$refs.mypass.getInput().style.borderClolr = 'green'
        this.$refs.myname.getInput().style.border = '2px solid red'
      }
    },
    'loginForm.password': function (newval) {
      // console.log(newval, oldval)
      if (newval.trim().length < 6) {
        // console.log('red')
        this.$refs.mypass.getInput().style.border = '2px solid red'
      } else {
        // console.log('green')
        this.$refs.mypass.getInput().style.border = '2px solid green'
      }
    }
  }
}
</script>

<style lang="less" scoped>
.login-container {
  height: 100%;
  background-color: #2b4b6b;
  overflow: hidden;
  .login-box {
    width: 450px;
    height: 304px;
    background-color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 4px;

    .avatar-box {
      width: 130px;
      height: 130px;
      padding: 8px;
      border-radius: 50%;
      border: 1px solid #eee;
      background-color: #fff;
      // 
      box-shadow: 0 0 10px #eee;
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      // 
      user-select: none;
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background-color: #eee;
      }
    }

    .el-form {
      position: absolute;
      bottom: 0;
      width: 100%;
      padding: 20px;
      // 20pxinput
      box-sizing: border-box;
    }
  }
}
</style>
