<template>
  <div>
    <!-- BreadCrumb.vue-------------- -->
    <bread-crumb one="" two="" three="categories"></bread-crumb>

    <!-- Dialog -->
    <el-dialog
      title=""
      :visible.sync="addDialog"
      width="50%"
      @close="resetForm()"
    >
      <el-form ref="addFormRef" :rules="addFormRules" :model="addForm" label-width="110px">
        <!-- ------------------------- -->
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addForm.cat_name" ref="mycat_name" clearable @clear="getCateList()"></el-input>
        </el-form-item>
        <!-- ------------------------- -->
        <el-form-item label="" prop="selectTwoCate">
          <!-- --------------------- -->
          <el-cascader v-model="addForm.selectTwoCate" :options="cateListTwo" :props="{ expandTrigger: 'hover', value: 'cat_id', label: 'cat_name', checkStrictly:true }" @active-item-change="handleChange" @change="handleChange" clearable></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialog = false"> </el-button>
        <el-button type="primary" @click="addCate()"></el-button>
      </span>
    </el-dialog>

    <!-- -------------------------------------- -->
    <el-card class="box-card">
      <!-- --------------------------------- -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddDialog()"></el-button>
        </el-col>
      </el-row>

      <!-- table----------------------------- -->
      <el-table :data="cateList" style="width: 100%;" border row-key="cat_id">
        <!-- ---------------------------- -->
        <el-table-column type="index" label="" width="50"></el-table-column>
        <!-- ------------------------ -->
        <el-table-column prop="cat_name" label="" width="330"></el-table-column>
        <!-- ---------------------------- -->
        <el-table-column prop="mobile" label="" width="280">
          <i class="el-icon-circle-check" style="color: rgb(68, 178, 170);"></i>
        </el-table-column>
        <!-- ---------------------------- -->
        <el-table-column prop="cat_level" label="" width="280">
          <template slot-scope="stDate">
            <el-tag v-if="stDate.row.cat_level===0"></el-tag>
            <el-tag type="success" v-else-if="stDate.row.cat_level===1"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>

        <!-- ---------------------------------- -->
        <el-table-column label="" width="296">
          <!-- stDatarow/id -->
          <template slot-scope="stData">
            <!-- -------------------------------- -->
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(stData.row)"
            ></el-button>
            <!-- -------------------------------- -->
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="delUser(stData.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // ---------------------------------------
      cateList: [], // 

      reqParam: {
        type: 3, // 
        pagenum: 1, // 1
        pagesize: 5 // 5
      },

      // --------------------------------
      addDialog: false, // 
      // Form
      addForm: {
        cat_name: '',
        // addForm
        selectTwoCate: [],
        cat_pid: 0, // 
        cat_level: 0 // 
      },
      // 
      addFormRules: {
        cat_name: [
          // { required: , message: '', trigger: '' }
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      cateListTwo: []

    }
  },

  methods: {
    // 
    async getCateList () {
      var { data: dt } = await this.$http.get('categories', {
        params: this.reqParam
      })
      // console.log(dt)
      // cateList
      this.cateList = dt.data.result
    },
    // 
    /* indexMethod (index) {
      return index++
    }, */

    // 
    async showAddDialog () {
      this.addDialog = true // 
      // (1-2)
      var { data: dt } = await this.$http.get('categories', { params: { type: 2 } })
      console.log(dt)
      // data
      this.cateListTwo = dt.data
    },

    // 
    handleChange () {
      // selectTwoCatpidlevel
      // selectTwoCate0 1 2
      var cateArr = this.addForm.selectTwoCate
      // 
      console.log(this.addForm.selectTwoCate) // []cateArr.length=2
      if (cateArr.length === 0) {
        this.addForm.cat_pid = 0
        this.addForm.cat_level = 0
      } else if (cateArr.length === 1) {
        this.addForm.cat_pid = cateArr[0]
        this.addForm.cat_level = 1
      } else {
        this.addForm.cat_pid = cateArr[1]
        this.addForm.cat_level = 2
      }
    },

    // 
    addCate () {
      this.$refs.addFormRef.validate(async valid => {
        if (valid) {
          var { data: dt } = await this.$http.post('categories', this.addForm)
          // console.log(dt)
          this.addDialog = false
          this.$message.success(dt.meta.msg)
          this.getCateList()
        }
      })
    },

    // form/
    resetForm () {
      this.$refs.addFormRef.resetFields()
      this.addForm.cat_pid = 0
      this.addForm.cat_level = 0
    }

  },

  // getCateList
  created () {
    this.getCateList()
  }

}

</script>

<style lang="less" scoped>
</style>
