<template>
  <el-container>
    <el-header>
      <div class="logo-box">
        <img src="../assets/img/logo.png" alt />
        <span>VUE</span>
      </div>
      <el-button type="danger" @click="logout()"></el-button>
    </el-header>
    <el-container>
      <!--  -->
      <el-aside :width="fold?'65px':'200px'">
        <div class="toggle_bar" @click="fold=!fold">|||</div>
        <el-menu
          background-color="#333744"
          text-color="#fff"
          active-text-color="#409eff"
          unique-opened
          :collapse="fold"
          :collapse-transition="false"
          router
        >
        <!--  -->
          <el-submenu v-for="item in menuList" :key="item.id" :index="item.id+''" :style="{width:fold?'65px':'200px'}">
            <template slot="title">
              <i :class="'iconfont icon-'+iconList[item.path]"></i>
              <span>{{item.authName}}</span>
            </template>
            <!--  -->
            <el-menu-item v-for="item2 in item.children" :key="item2.id" :index="item2.path">
              <template slot="title">
              <i class="el-icon-menu"></i>
              <span>{{item2.authName}}</span>
            </template>
            </el-menu-item>
          </el-submenu>

        </el-menu>
      </el-aside>
      <el-main>
        <!-- welcome -->
        <!-- <router-view></router-view> -->
        <router-view />
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  // 
  created () {
    // axiostoken
    this.getMenuList()
  },
  methods: {
    // token
    logout () {
    // 
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // token
        window.sessionStorage.clear()
        // push
        this.$router.push('/login')
      }).catch(() => {})
    },
    // 
    async getMenuList () {
      // axiosurl
      // var { data: dt }
      var { data: dt } = await this.$http.get('menus')
      // console.log(dt)

      // 
      /* if (dt.meta.status !== 200) {
        return this.$message.error(dt.meta.msg)
      } */

      // menuList
      this.menuList = dt.data
    }
  },
  data () {
    return {
      // falsetrue
      fold: false,
      // 
      menuList: [],
      // datapath
      iconList: {
        // path
        users: 'users',
        rights: 'tijikongjian',
        goods: 'shangpin',
        orders: 'danju',
        reports: 'baobiao'
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-container {
  height: 100%;
  .toggle_bar{
    height: 25px;
    background: #4a5064;
    text-align: center;
    color: #fff;
    font-size: 12px;
    line-height: 25px;
    user-select: none;
    cursor: pointer;
    letter-spacing: .1rem /* 5/50 */;
  }
  .el-header {
    background-color: #373d41;
    padding: 0;
    display: flex;
    align-items: center;
    // 
    justify-content: space-between;
    padding-left: 10px;
    padding-right: 20px;
    .logo-box {
      color: #fff;
      font-size: 22px;
      user-select: none;
      display: flex;
      align-items: center;
      img {
        width: 50px;
        height: 50px;
        margin-right: 10px;
      }
    }
  }
  .el-aside {
    background-color: #333744;
  }
  .el-main {
    background-color: #eaedf1;
  }
}
</style>
