<template>
  <div>
    <!-- BreadCrumb.vue-------------- -->
    <bread-crumb one="" two="" three="goods"></bread-crumb>

    <!-- -------------------------------------- -->
    <el-card class="box-card">
      <!---->
      <el-row :gutter="20">
        <el-col :span="8">
          <!-- script -->
          <el-input placeholder="" v-model="reqParam.query" clearable @clear="search" @keyup.enter.native="search">
            <!-- -------------------------------- -->
            <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="$router.push('/goodsadd')"></el-button>
        </el-col>
      </el-row>

      <!---->
      <el-table :data="goodsList" stripe style="width: 100%" border>
        <el-table-column type="index" label="" width="50"></el-table-column>
        <el-table-column prop="goods_name" label=""></el-table-column>
        <el-table-column prop="goods_price" label="()" width="100"></el-table-column>
        <el-table-column prop="goods_weight" label="" width="80"></el-table-column>
        <el-table-column label="" width="190">
          <template slot-scope="stData">
            <span>{{ stData.row.add_time | dateFormat('') }}</span>
          </template>
        </el-table-column>
        <el-table-column prop label="" width="150">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="delGoods(scope.row.goods_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- ----------------------------------------- -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="reqParam.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="reqParam.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="sum"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      goodsList: [], // 
      // 
      sum: 0, // 
      reqParam: {
        query: '', // 
        pagenum: 1, // 1
        pagesize: +window.localStorage.getItem('pagenum') || 3 // localStorage   3
      }
    }
  },

  created () {
    this.getGoodsList()
  },

  methods: {
    // 
    async delGoods (goodsId) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          const { data: dt } = await this.$http.delete('goods/' + goodsId)
          // console.log(dt)
          // 
          this.$message.success(dt.meta.msg)

          // 
          if (this.reqParam.pagenum !== 1 && this.userList.length === 1) {
            this.reqParam.pagenum--
          }
          // 
          this.getGoodsList()
        })
        .catch(() => {})
    },

    // 
    search () {
      this.reqParam.pagenum = 1
      this.getGoodsList()
    },

    // 
    handleSizeChange (newsize) {
      this.reqParam.pagesize = newsize
      // newsizepagenumlocalStorage
      window.localStorage.setItem('pagenum', newsize)
      this.getGoodsList()
    },

    // 
    handleCurrentChange (newpage) {
      this.reqParam.pagenum = newpage
      this.getGoodsList()
    },

    // 
    async getGoodsList () {
      var { data: res } = await this.$http.get('goods', {
        params: this.reqParam
      })

      // console.log(res)
      this.goodsList = res.data.goods
      // 
      this.sum = res.data.total
    }
  },

  // 
  filters: {
    dateFormat (origin, addr) {
      // console.log(origin) // 
      // origin
      // var tm = new Date(/)
      // 
      var tm = new Date(origin)
      var yyyy = tm.getFullYear()
      var mm = tm.getMonth() + 1
      var dd = tm.getDate()
      var hh = tm.getHours()
      var ii = tm.getMinutes()
      var ss = tm.getSeconds()

      return `${addr}${yyyy}-${mm}-${dd} ${hh}:${ii}:${ss}`
    }
  }
}
</script>

<style lang="less" scoped>

</style>
