import Vue from 'vue'
import App from './App.vue'
import router from './router'

// CSS
import './assets/css/global.css'
// ElementUI
// import ElementUI from 'element-ui'

// ElementUICSS
import 'element-ui/lib/theme-chalk/index.css'

// BreadCrumb.vue
import BreadCrumb from './components/BreadCrumb.vue'

// ElementUIimport
import {
  Pagination,
  Dialog,
  Menu,
  Submenu,
  MenuItem,
  Input,
  Checkbox,
  CheckboxGroup,
  Switch,
  Select,
  Option,
  Button,
  Table,
  TableColumn,
  Tooltip,
  Breadcrumb,
  BreadcrumbItem,
  Form,
  FormItem,
  Tabs,
  TabPane,
  Tag,
  Alert,
  Icon,
  Row,
  Col,
  Upload,
  Card,
  Steps,
  Step,
  Cascader,
  Container,
  Header,
  Aside,
  Main,
  MessageBox,
  Message
} from 'element-ui'

// NProgressJSCSS
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

// axios:
import axios from 'axios'

// 
NProgress.configure({ showSpinner: false })

// BreadCrumb.vue
Vue.component('bread-crumb', BreadCrumb)

// :
axios.defaults.baseURL = 'http://127.0.0.1:11333/api/private/v1/'
// 
// console.dir(axios)
axios.interceptors.request.use(function (config) {
  // console.log(config)
  NProgress.start() // 
  // axiostokentokenaxiosAuthorizationtoken
  var token = window.sessionStorage.getItem('token')
  config.headers.Authorization = token
  return config
}, function (error) {
  return Promise.reject(error)
})

// axios
Vue.prototype.$http = axios

// ElementUI
// Vue.use(ElementUI)

Vue.config.productionTip = false

// 
Vue.use(Pagination)
Vue.use(Dialog)
Vue.use(Menu)
Vue.use(Submenu)
Vue.use(MenuItem)
Vue.use(Input)
Vue.use(Checkbox)
Vue.use(CheckboxGroup)
Vue.use(Switch)
Vue.use(Select)
Vue.use(Option)
Vue.use(Button)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Tooltip)
Vue.use(Breadcrumb)
Vue.use(BreadcrumbItem)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Tabs)
Vue.use(TabPane)
Vue.use(Tag)
Vue.use(Alert)
Vue.use(Icon)
Vue.use(Row)
Vue.use(Col)
Vue.use(Upload)
Vue.use(Card)
Vue.use(Steps)
Vue.use(Step)
Vue.use(Cascader)
Vue.use(Container)
Vue.use(Header)
Vue.use(Aside)
Vue.use(Main)

// 
Vue.prototype.$confirm = MessageBox.confirm
Vue.prototype.$message = Message

// Vue
new Vue({
  // 
  created () {
    // Vue$messageVue
    axios.interceptors.response.use(response => {
      NProgress.done() // 
      // :
      // responseaxios>await
      // responseconfigdataheaderrequeststatusstatusText
      // console.log(response)

      // 200()/201()
      if (response.data.meta.status !== 200 && response.data.meta.status !== 201) {
        console.log(this) // thisVue
        return this.$message.error(response.data.meta.msg)
      }

      return response
    }, function (error) {
      return Promise.reject(error)
    })
  },
  router,
  render: h => h(App)
}).$mount('#app')
