<template>
  <div>
    <!-- BreadCrumb.vue-------------- -->
    <bread-crumb one="" two="" three="params"></bread-crumb>

    <!-- -------------------------------------- -->
    <el-card class="box-card">
      <!-- --------------------------- -->
      <el-alert title="" type="warning" show-icon></el-alert>

      <!--  ------------------------------ -->
      <el-row>
        <el-col>
          <span> </span>
          <!-- --------------------- -->
          <el-cascader v-model="selectThreeCate" :options="cateList" :props="{ expandTrigger: 'hover', value: 'cat_id', label: 'cat_name' }" @active-item-change="handleChange" @change="handleChange" clearable></el-cascader>
        </el-col>
      </el-row>

      <!-- tabs-------------------------- -->
        <el-tabs v-model="activeName" @tab-click="handleClick">

          <!-- ----------------------- -->
          <el-tab-pane label="" name="many">
            <el-button type="primary" :disabled="disBtn" size="mini"></el-button>
            <!-- --------------------- -->
            <el-table :data="manyParamList">
              <!-- ------------------------- -->
              <el-table-column type="expand">
                <template slot-scope="stData">
                  <!-- Tag ------------------- -->
                  <el-tag v-for="item in stData.row.vals_arr" :key="item"  closable>{{item}}</el-tag>
                </template>
              </el-table-column>

              <!-- ------------------- -->
              <el-table-column label="" type="index" width="100"></el-table-column>
              <el-table-column label="" prop="attr_name"></el-table-column>
              <el-table-column label="" width="240">
                <el-button size="mini" type="primary" icon="el-icon-edit" @click='showEditDialog(stData.row)'></el-button>
                <el-button size="mini" type="danger" icon="el-icon-delete" @click='showEditDialog(stData.row).id'></el-button>
              </el-table-column>

            </el-table>
          </el-tab-pane>

          <!-- ---------------------- -->
          <el-tab-pane label="" name="only">
            <el-button type="primary" :disabled="disBtn" size="mini"></el-button>
            <!-- -------------------- -->
            <el-table :data="onlyParamList">
              <!-- ----------------------- -->
              <el-table-column type="expand">
                <template slot-scope="stData">
                  <!-- Tag ------------------- -->
                  <el-tag v-for="item in stData.row.vals_arr" :key="item"  closable>{{item}}</el-tag>
                </template>
              </el-table-column>

              <!-- -------------------- -->
              <el-table-column label="" type="index" width="100"></el-table-column>
              <el-table-column label="" prop="attr_name"></el-table-column>
              <el-table-column label="" width="240">
                <el-button size="mini" type="primary" icon="el-icon-edit" @click='showEditDialog(stData.row)'></el-button>
                <el-button size="mini" type="danger" icon="el-icon-delete" @click='showEditDialog(stData.row).id'></el-button>
              </el-table-column>
            </el-table>
          </el-tab-pane>

      </el-tabs>
    </el-card>
  </div>
</template>

<script>
export default {
  // 
  created () {
    this.getCateList()
  },

  data () {
    return {
      // 
      cateList: [],
      // 
      selectThreeCate: [],
      // 
      activeName: 'only',
      // true / false
      disBtn: true,
      // 3id
      selectCateId: 0,
      // 
      manyParamList: [], // 
      onlyParamList: [] // 

    }
  },

  methods: {
    // 
    handleChange () {
      // len
      var len = this.selectThreeCate.length
      // 
      if (len === 3) {
        // 3
        this.disBtn = false
        // 3idselectCateId
        this.selectCateId = this.selectThreeCate[2]
        // 
        this.getParamList()
      } else {
        // 3
        this.disBtn = true
        // 2
        this.selectThreeCate = []
        // selectCateId
        this.selectCateId = 0
        // 3()
        this.manyParamList = []
        this.onlyParamList = []
      }
    },

    // 
    async getCateList () {
      var { data: dt } = await this.$http.get('categories', {
        params: { type: 3 }
      })
      // console.log(dt)
      // cateList
      this.cateList = dt.data
    },

    // 
    async getParamList () {
      var { data: dt } = await this.$http.get('categories/' + this.selectCateId + '/attributes', { params: { sel: this.activeName } })
      console.log(dt)

      // attr_vals
      dt.data.forEach(element => {
        element.vals_arr = element.attr_vals.split(' ') // vals_arr
      })
      // only / many
      if (this.activeName === 'many') {
        this.manyParamList = dt.data
      } else {
        this.onlyParamList = dt.data
      }
    },

    // 
    handleClick () {
      // 
      if (this.selectCateId !== 0) {
        this.getParamList()
      }
    }

  }
}
</script>

<style lang="less" scoped>
.el-row{
  margin-top: 15px;
}
.el-tag{
  margin: 10px 5px;
}
</style>
